/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */
html, body {
  font-family: "Roboto", "Helvetica", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #fff;
  height: 100%;
  min-width: 350px;
  overflow-x: hidden;
  max-width: 1900px;
}

nav {
  background: #FFFFFF;
}

.p-imagen {
  width: 100%;
  height: 300px;
  object-fit: cover;
}

.links {
  cursor: pointer;
}

.icon_menu {
  width: 40px;
  height: 60px;
  -webkit-filter: grayscale(100%) contrast(0%);
          filter: grayscale(100%) contrast(0%);
}

.azul-orbis {
  color: #335D87;
}

.backg {
  background-image: url("../images/background/main_background.png");
  background-size: cover;
}

.btn.btn-contact {
  text-decoration: none;
  background-color: #335D87;
  border-radius: 25px;
  color: white;
  border: none;
}

.back-form {
  background-image: url("../images/svg/rec_main.svg");
  background-size: auto auto;
}

.bg-services {
  background: #EFC324;
  border-radius: 0 0 30px 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}

.product-types {
  background: #FFFFFF;
}

.card {
  min-width: 350px;
  min-height: 470px;
  width: 350px;
  height: 470px;
  background: #F6F6F6;
  border-radius: 25px;
  box-shadow: 5px 4px 15.5px rgba(0, 0, 0, 0.25);
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.card_store {
  min-width: 350px;
  min-height: 420px;
  width: 350px;
  height: 420px;
  background: #F6F6F6;
  border-radius: 25px;
  box-shadow: 5px 4px 15.5px rgba(0, 0, 0, 0.25);
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.img_card_store {
  width: 100%;
  position: absolute;
  top: -17%;
}

.img_card_store_2 {
  width: 100%;
  position: absolute;
  top: -65%;
}

.img_card_store_3 {
  width: 150%;
  position: absolute;
  top: -7%;
  right: 0%;
}

.bg-name-store {
  background: #efc324;
}

.text-yellow-orbis {
  color: #efc324;
}

.name_medium {
  top: 39%;
}

.cards {
  padding: 1.5rem;
}

.card_image {
  left: 25%;
  top: -18%;
}

.row-gap-card {
  row-gap: 8rem !important;
}

.bg_info {
  width: 400px;
  height: 550px;
  background-image: url("../images/background/info_background.jpg");
  background-size: cover;
  background-position-x: 25%;
}

.img_info {
  max-width: 700px;
  width: 100%;
  height: 50vh;
  background-image: url("../images/background/info_background.jpg");
  background-size: cover;
  background-position-y: -20px;
}

.bg_azul_info {
  max-width: 100%;
  max-height: 100vh;
  height: 30vh;
  background-image: url("../images/background/main_azul_background.png");
  background-size: cover;
  background-repeat: no-repeat;
}

footer {
  background-color: #F6F6F6;
  max-height: 380px;
  height: 350px;
}

.bg_azul_contact {
  max-width: 100%;
  max-height: 100%;
  min-height: 40vh;
  height: 56vh;
  background-image: url("../images/background/question_background.png");
  background-size: cover;
  background-repeat: no-repeat;
}

.img_brand {
  width: 100px;
}

.sh-inset {
  border: none;
  box-shadow: inset 0.6px 0.6px 3px rgba(0, 0, 0, 0.9);
}

.text-shadow {
  text-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
}

@media screen and (min-width: 768px) {
  .img_brand {
    width: 250px;
  }
  .backg {
    background-size: 1572px 1004px !important;
    background-position: left center;
  }
  .bg_azul_contact {
    background-size: 1004px 566px !important;
    background-position: right center;
    height: 68vh !important;
  }
  .line-med {
    line-height: 3;
  }
}
@media screen and (min-width: 1000px) {
  .backg {
    background-size: 3072px 2504px !important;
  }
}